





































import Vue from "vue";

export default Vue.extend({
  name: "LabelsFinder",
  data: function() {
    return {
      selection: this.$store.state.product as string,
      products: [] as string[]
    };
  },
  beforeCreate: function() {
    // Fetch labels catalog if not fetched already
    if (!window._storage) {
      const appVersion = (window._app && window._app.app_version) || "2";
      const d = document;
      const catalog = d.createElement("script");
      catalog.src =
        "https://cdn.jsdelivr.net/gh/frenchcooc/labelmaker-landing@" +
        appVersion +
        "/dist/js/catalog.min.js";
      catalog.async = true;
      catalog.defer = true;
      d.body.appendChild(catalog);
    }
  },
  mounted: function() {
    this.fetchCatalog();
  },
  methods: {
    labelCreationUI: function() {
      this.$store.dispatch("setProduct", this.selection);
      this.$store.dispatch("setLocation", "/");
    },

    closeUI: function() {
      window._frontend.close();
    },

    fetchCatalog: function() {
      if (!window._storage) {
        return window.setTimeout(this.fetchCatalog, 200);
      }

      const allLabels = window._storage.ALL_LABELS;
      const n = allLabels.length;
      const products = [];
      for (let i = 0; i < n; i++) {
        products.push(allLabels[i][1]);
      }

      this.products = products.sort();
    }
  }
});
